import request from '../utils/request.js'

// 首页数据
export const getHome = () => {
	return request({
		url:'/home/index'
	})
}

// 头部栏
export const getHead = () => {
	return request({
		url:'/home/category/head'
	})
}

// 广告轮播图
export const getBanner = () => {
	return request({
		url:'/home/banner'
	})
}

// 新鲜好物
export const getNew = () => {
	return request({
		url:'/home/new'
	})
}

// 人气推荐
export const getHot = () => {
	return request({
		url:'/home/hot'
	})
}

// 热门品牌
export const getBeand = () => {
	return request({
		url:'/home/brand'
	})
}

// 获取品牌详情
export const getBrandId = (id) => {
	return request({
		url:`/brand/${id}`
	})
}
